 font-family: 'Handlee';  
 box-sizing: border-box;
}

html {
    width: 100%;
    height: 100%;
    margin: 0px;
}

h1 {
    margin: 0;
}


body {
    width: 100%;
    height: 100%;
    margin: 0px;
    color: #004e64;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1rem;
    border-bottom: 2px solid rgba(0, 78, 100, 0.1);
    gap: 2rem;
}



#currentProject {
    margin: 0;
}

.title {
    display: flex;
    flex-direction: column;
}

.menu-icon {
    font-size: 3rem;
    display: inline-block;
    width: auto;
    height: auto;
    cursor: pointer;
    font-weight: 800;
}

#new-task-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 2rem;
    gap: 10px;
    font-size: 2rem;
    cursor: pointer;      
}

#content {
    display: flex;
}
.sidebar {
    height: 100%;
    border-right: 2px solid rgba(0, 78, 100, 0.1);
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 2rem 0;
}

#content {
    height: 100%;
    width: 100%
}

#sidebar-container {
    height: 100%;
    width:20%;
}

#add-icon {
    font-size: 2.5rem;
    font-weight: 900;
}

#calendar-icon,
#today-icon,
#edit-icon {
    font-size: 2.5rem;
}

#new-task-button span:last-child {
    margin-bottom: -6px;
}

.sidebar-nav {
    display: flex;
    padding: 1rem;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.sidebar-nav:hover,
#menu:hover,
#new-task-button:hover,
#delete-icon:hover,
#edit-task-icon:hover {
    filter: brightness(1.6);
    cursor: pointer;
}



.count {
    margin-left: auto;
}

.hidden {
    display: none !important;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    backdrop-filter: blur(5px);
    font-size: 1.5rem;
}

.modal-content {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    width: 90%;
    max-width: 450px;
    box-shadow: 0 10px 25px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.priority-selector {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.priority-radio {
    display: none;
}

.priority-radio:checked + .flag-icon {
    transform: scale(1.4);
    font-variation-settings: 'FILL' 1;
    filter: drop-shadow(0 0 5px rgba(0,0,0,0.2));
} 


.flag-icon {
    cursor: pointer;
    transition: transform 0.2s;
}

.flag-red, .high { color: #ff0000; }
.flag-orange, .medium { color: #ffa500; }
.flag-green, .low { color: #008000; }

.high, .medium, .low {
    font-variation-settings: 'FILL' 1;
}

.edit-btn, 
.delete-btn,
.submit-btn {
    padding: 0.8rem;
    background-color: #004e64;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    flex: 1;
}

.delete-btn {
    background-color: red;
}

#modal-close {
    align-self: end;
    font-weight: bold;
    cursor: pointer;
}

.btn-container {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
}

.form-input,
.form-text-area,
.form-date {
    padding: 5px;
    font-size: 1rem;
}

.modal-heading {
    font-weight: bold;
    font-size: 2rem;
}

#my-projects-container {
    
    padding-left: 22px;
   
}

.my-projects-heading {
    font-weight: 800;
    font-size: 1.2rem;
}

#my-projects-container {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1rem;
}

.my-projects-list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.project-list-item {
    padding: 6px 0;
}

.task-card {
    display: flex; 
    gap: 1rem; 
    flex-wrap: wrap;
    max-width: 900px;
    width: 100%;
    padding: 1rem;
    border: 1px solid #004e64;
    align-items: center;
    border-radius: 12px;
}

.display-container {
    height: 100%;
    width: 80%;
    display: flex; 
    flex-direction: column; 
    gap: 1rem;
    padding: 1rem 2rem;
    margin-left: auto;
    align-items: center;
    
}

.title-container { 
    display: flex;
    align-items: center;
    gap: 1rem;
    max-width: 900px;
    width: 100%;
    
}

.app-title {
    margin-right: auto;
}

.task-card div:last-of-type {
    margin-left: auto;
}

.task-title {
    font-weight: bold;
}

.task-complete:checked + .task-text-container {
    text-decoration: line-through;
    opacity: 0.5;
}

.task-complete:checked ~ #delete-icon {
    display: block;
    cursor: pointer;
}

#delete-icon {
    display: none;
    
}

